@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.body {
  margin: 0;
  padding: 0;
}

.content {
  background-color: #ffffff;
  font-family: "PT Sans", "Tahoma", sans-serif;
  margin: 0 auto;
  padding: 0;
  width: 320px;
}
@media (min-width: 768px) {
  .content {
    min-width: 768px;
  }
}
@media (min-width: 1200px) {
  .content {
    min-width: 1200px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.welcome {
  background-repeat: no-repeat;
  background-color: #81b3d2;
  background-size: cover;
  box-sizing: border-box;
  padding: 88px 18px;
  margin: 0;
  margin-top: 56px;
  position: relative;
  /*stylelint-disable*/
  background-image: url("../img/img-index/rocks-sedona-320.jpg");
  background-image: -webkit-image-set(url("../img/img-index/rocks-sedona-320.webp") 1x, url("../img/img-index/rocks-sedona-320.jpg") 1x, url("../img/img-index/back-mobile@2x.webp") 2x, url("../img/img-index/back-mobile@2x.jpg") 2x);
  /*stylelint-enable*/
}
@media (min-width: 768px) {
  .welcome {
    margin-top: 0;
    padding: 78px 32px 92px 32px;
    /*stylelint-disable*/
    background-image: url("../img/img-index/back-tablet.jpg");
    background-image: -webkit-image-set(url("../img/img-index/back-tablet.webp") 1x, url("../img/img-index/back-tablet.jpg") 1x, url("../img/img-index/back-tablet@2x.webp") 2x, url("../img/img-index/back-tablet@2x.jpg") 2x);
    /*stylelint-enable*/
  }
}
@media (min-width: 1200px) {
  .welcome {
    padding: 78px 50px 96px 50px;
    /*stylelint-disable*/
    background-image: url("../img/img-index/back-desktop.jpg");
    background-image: -webkit-image-set(url("../img/img-index/back-desktop.webp") 1x, url("../img/img-index/back-desktop.jpg") 1x, url("../img/img-index/back-desktop@2x.webp") 2x, url("../img/img-index/back-desktop@2x.jpg") 2x);
    /*stylelint-enable*/
  }
}

.welcome::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 2;
  height: 1px;
  border-width: 1px 160px 28px;
  border-style: solid;
  border-color: #ffffff #ffffff transparent;
  transform: rotate(180deg);
}
@media (min-width: 768px) {
  .welcome::after {
    border-width: 1px 350px 56px;
    left: 50%;
    transform: translateX(-50%) rotate(180deg);
  }
}
@media (min-width: 1200px) {
  .welcome::after {
    border-width: 4px 352px 61px;
    left: 50%;
    transform: translateX(-50%) rotate(180deg);
  }
}

@media (min-width: 768px) {
  .welcome::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 2;
    height: 1px;
    border-width: 1px 384px 36px;
    border-style: solid;
    border-color: #ffffff #ffffff transparent;
    transform: rotate(180deg);
  }
}
@media (min-width: 1200px) {
  .welcome::before {
    border-width: 1px 600px 64px;
  }
}

.welcome__image {
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .welcome__image {
    display: block;
    width: 464px;
    height: max-content;
  }
}

.page-nav {
  padding: 0;
  position: relative;
  z-index: 2;
}
@media (min-width: 768px) {
  .page-nav {
    padding: 20px 10px 14px 15px;
    background-color: #ffffff;
  }
}

.page-nav--closed .page-nav__list {
  display: none;
}
@media (min-width: 768px) {
  .page-nav--closed .page-nav__list {
    display: grid;
  }
}

.page-nav--opened {
  position: absolute;
  width: 320px;
  top: 0;
  margin-top: 0;
}
@media (min-width: 768px) {
  .page-nav--opened {
    position: relative;
    width: 100%;
    box-sizing: border-box;
  }
}

.page-nav--nojs .page-nav__list,
.page-nav--opened .page-nav__list {
  display: block;
}
@media (min-width: 768px) {
  .page-nav--nojs .page-nav__list,
.page-nav--opened .page-nav__list {
    display: grid;
  }
}

.page-nav__toggle {
  position: absolute;
  z-index: 4;
  top: -56px;
  right: 10px;
  border: none;
  outline: none;
  padding: 20px 20px;
  box-sizing: border-box;
  background-color: transparent;
}
.page-nav__toggle:hover, .page-nav__toggle:focus {
  opacity: 0.6;
}
.page-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .page-nav__toggle {
    display: none;
  }
}

.page-nav--closed .page-nav__toggle::before {
  content: "";
  position: absolute;
  z-index: 4;
  top: 17px;
  right: 50%;
  transform: translateX(50%);
  width: 24px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
  cursor: pointer;
}

.page-nav--opened .page-nav__toggle::before, .page-nav--opened .page-nav__toggle::after {
  content: "";
  position: absolute;
  top: 82px;
  right: 50%;
  width: 24px;
  height: 4px;
  background-color: #ffffff;
}
.page-nav--opened .page-nav__toggle::before {
  transform: translateX(50%) rotate(45deg);
  box-shadow: none;
}
.page-nav--opened .page-nav__toggle::after {
  transform: translateX(50%) rotate(-45deg);
}
.page-nav--opened .page-nav__toggle:hover, .page-nav--opened .page-nav__toggle:focus {
  opacity: 0.6;
}
.page-nav--opened .page-nav__toggle:active {
  opacity: 0.3;
}

.page-nav--nojs > .page-nav__toggle {
  display: none;
}

.page-nav--nojs {
  margin-top: 96px;
}
@media (min-width: 768px) {
  .page-nav--nojs {
    margin: 0;
  }
}

.page-nav--nojs ~ .welcome,
.page-nav--nojs ~ .page-header__wrap {
  margin-top: 0;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .nav-list {
    background-color: #ffffff;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(5, 1fr);
  }
}

@media (min-width: 768px) {
  .nav-list li:nth-of-type(4n-1) {
    grid-column: 4/5;
  }
}

.nav-list__item {
  font-family: "PT Sans", "Tahoma", sans-serif;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  border-bottom: 1px rgba(1, 1, 1, 0.2) solid;
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .nav-list__item {
    font-size: 14px;
    line-height: 21px;
    border-bottom: none;
    box-shadow: none;
  }
}

.nav-list li:last-of-type {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 85%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .nav-list li:last-of-type {
    background: none;
  }
}

.nav-list__link {
  appearance: none;
  text-decoration: none;
  color: #ffffff;
  display: block;
  margin: 0 auto;
  padding: 17px 30px 11px 30px;
}
.nav-list__link:hover {
  background-color: #669ec0;
  outline: none;
}
.nav-list__link:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
  appearance: none;
}
@media (min-width: 768px) {
  .nav-list__link {
    color: #000000;
    padding: 0;
    width: auto;
    margin: auto;
  }
  .nav-list__link:hover {
    background-color: #ffffff;
    color: #81b3d2;
  }
  .nav-list__link:active {
    background-color: #ffffff;
    color: #000000;
    opacity: 0.3;
  }
}

@media (min-width: 768px) {
  .nav-list__link--current {
    color: #766357;
  }
}

