.user-data {
  margin: 16px 0 20px 0;
  padding: 0;
}

.user-data__label {
  font-family: "PT Sans", "Tahoma", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-transform: uppercase;
  text-align: start;
  font-weight: 400;
  display: block;
  margin: 0 0 3px 16px;

  @media (min-width: $tablet-width) {
    margin: 0 0 2px 16px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 2px 18px;
  }
}

.user-data__input-user {
  font-family: "PT Sans", "Tahoma", sans-serif;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  font-weight: 700;
  text-transform: none;
  text-align: start;
  display: block;
  padding: 13px 60px 13px 16px;
  box-sizing: border-box;
  background-color: #f2f2f2;
  outline: none;
  border: none;
  margin: 0;
  width: 100%;

  &:hover {
    background-color: #e5e5e5;
  }

  &:focus,
  &:active {
    background-color: $white;
    outline: 4px solid #e5e5e5;
    outline-offset: -4px;
  }
}

.user-data__icon {
  background-repeat: no-repeat;
  background-position: right;
  z-index: 2;
}

.user-data__icon--phone {
  background-image: url("../img/phone.svg");
}

.user-data__icon--email {
  background-image: url("../img/mail.svg");
}


.user-data__input-user::placeholder {
  font-family: "PT Sans", "Tahoma", sans-serif;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  text-align: start;
  opacity: 30%;
  text-transform: none;
  font-weight: 700;
  width: 70%;
}
