.advantages {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
  background-color: #eeeeee;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: -18px;
    height: 1px;
    border-width: 1px 160px 18px;
    border-style: solid;
    border-color: $blue $blue transparent;
    transform: rotate(180deg);

    @media (min-width: $tablet-width) {
      display: none;
    }
  }

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding-bottom: 10px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 0;
  }
}

.advantages__item {
  @media (min-width: $tablet-width) {
    align-self: center;
  }
}

.advantages__title {
  font-family: "PT Sans", "Tahoma", sans-serif;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: 21px;
    margin: 0 auto 14px auto;
  }
}

.advantages__title--image {
  color: $white;
  margin-bottom: 10px;

  @media (min-width: $tablet-width) {
    margin-bottom: 14px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 0;
  }
}

.advantages__title--brige {
  margin-top: 20px;
}

.advantages__item--image {
  @media (min-width: $tablet-width) {
    grid-column: 1/-1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    background-color: $blue;
  }
}

.advantages__item--area {
  padding: 4px 14px 4px;
  margin: 0;
  text-align: center;
  position: relative;

  @media (min-width: $tablet-width) {
    padding: 0;
  }
}

.advantages__items {
  grid-column: 1/-1;
  background-color: $white;
  padding-bottom: 22px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    padding-bottom: 18px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 26px;
  }
}

.advantages__photo {
  display: block;
  object-fit: cover;
  height: 100%;

  @media (min-width: $tablet-width) {
    width: 512px;
  }

  @media (min-width: $desktop-width) {
    width: 800px;
  }
}

.advantages__photo--city {
  @media (min-width: $tablet-width) {
    grid-column: 2/4;
  }
}

.advantages__photo--bridge {
  @media (min-width: $tablet-width) {
    grid-column: 1/3;
  }
}

.advantages__order {
  font-family: "PT Sans", "Tahoma", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: $tablet-width) {
    margin: 0;
    margin-bottom: 24px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 22px;
  }
}

.advantages__order--image {
  color: $white;
  margin: 0;

  @media (min-width: $tablet-width) {
    margin-bottom: 8px;
  }
}

.advantages__description {
  font-family: "PT Sans", "Tahoma", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: $tablet-width) {
    margin: 0;
  }
}

.advantages__description--image {
  color: $white;
  margin: 0;
  padding: 16px 0;
  margin-bottom: 2px;

  @media (min-width: $desktop-width) {
    padding: 16px 24px;
  }
}

.advantages__description--bridge {
  @media (min-width: $desktop-width) {
    width: 230px;
    text-align: center;
    margin: 0 auto;
  }
}

.advantages__block {
  @media (min-width: $tablet-width) {
    display: block;
  }
}

.advantages__text {
  padding: 20px 20px 22px;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    padding: 40px 32px 34px;
  }

  @media (min-width: $desktop-width) {
    padding: 64px 50px 40px 50px;
  }
}

.advantages__text--road {
  @media (min-width: $desktop-width) {
    padding: 40px 50px 40px 50px;
  }
}

.advantages__text--image {
  background-color: $blue;

  @media (min-width: $tablet-width) {
    padding: 56px 32px 10px;
  }

  @media (min-width: $desktop-width) {
    padding: 26px 32px 10px 32px;
  }
}

.advantages__text--bridge {
  grid-column: 3/-1;
  grid-row: 1/-1;
  padding-top: 24px;

  @media (min-width: $tablet-width) {
    padding: 50px 30px 10px;
  }

  @media (min-width: $desktop-width) {
    padding: 20px 40px 4px 36px;
  }
}
